/*SECCION DE PROYECTOS WEB*/

.divFondo{
  width: 93%;
  height: 88%;
  position: absolute;
  bottom: 0;
  opacity: 0;
  padding: 10% 10%;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  
}
.divFondo h3{
  font-size: 2em;
  /*color: #337ab7;*/
  color: white;
}
.divFondo p{
 
  color: white;
  font-family: 'Rancho', cursive;
  font-size: 22px;
  text-align: justify;
}
.divFondo:hover{
  opacity: 1;
  border-radius: 10px;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  background-color: rgba(0,0,0,0.91); 
  
}

.botoncito{
    border-radius: 40px;
    background: white;
}

.ver{
    font-size: 20px;
}


